import { withPrefix } from "gatsby-link"
import { useBreakpoint } from "gatsby-plugin-breakpoints"
import React, { useEffect, useState } from "react"
import styled, { css } from "styled-components"

import { BREAK_POINT } from "components/sizes"

import ButtonImage from "images/common/subsidy-banner-close-button.svg"
import BannerPCImage from "images/common/subsidy-banner_pc.svg"
import BannerSPImage from "images/common/subsidy-banner_sp.svg"

const Container = styled.div`
  position: sticky;
  width: 100%;
  bottom: 0;
  padding-right: 24px;
  padding-bottom: 24px;
  pointer-events: none;
  @media (max-width: ${BREAK_POINT.w768}px) {
    bottom: 0px;
    padding-right: 0;
    padding-bottom: 0;
  }
`

const Contents = styled.div`
  display: flex;
  width: 100%;
  justify-content: flex-end;
`

const FadeIn = css`
  animation: fadeIn 1s ease 0s 1 normal;
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
`

const Banner = styled.a`
  ${FadeIn}
  pointer-events: auto;
  &:hover {
    transition: 0.3s;
    opacity: 0.8;
  }

  @media (max-width: ${BREAK_POINT.w768}px) {
    width: 100%;
  }
`

const BannerImg = styled.img`
  @media (max-width: ${BREAK_POINT.w768}px) {
    width: 100%;
    height: auto;
  }
`

const CloseButton = styled.button`
  ${FadeIn}
  position: absolute;
  pointer-events: auto;
  &:hover {
    opacity: 0.8;
  }

  @media (max-width: ${BREAK_POINT.w768}px) {
    width: 48px;
    height: 48px;
    &:hover {
      opacity: 0.5;
    }
  }
`

const CloseButtonImg = styled.img`
  position: absolute;
  top: -6px;
  right: -5px;

  @media (max-width: ${BREAK_POINT.w768}px) {
    top: 7px;
    right: 7px;
  }
`

interface ITIntroductionSubsidyFixedBannerProps {
  readonly onClick: () => void
}

export const ITIntroductionSubsidyFixedBanner: React.VFC<ITIntroductionSubsidyFixedBannerProps> = props => {
  const breakpoint = useBreakpoint()

  const [isScrolled, setIsScrolled] = useState<boolean>(false)
  const onScroll = () => setIsScrolled(true)
  useEffect(() => {
    window.addEventListener("scroll", onScroll)
    return () => window.removeEventListener("scroll", onScroll)
  }, [])

  const [hasPushedCloseButton, setHasPushedCloseButton] = useState<boolean>(false)

  // バナーを閉じるボタンのサイズ
  const closeButtonSize = breakpoint.sp ? 26 : 30

  if (!shouldRender(isScrolled, hasPushedCloseButton)) {
    return null
  }

  return (
    <Container>
      <Contents>
        <Banner href={withPrefix("/it-hojo/")} onClick={props.onClick}>
          {breakpoint.sp ? (
            <BannerImg
              src={BannerSPImage}
              alt="「IT導入補助金」のご活用により月額利用料の最大2年分の補助で1/4の実質費用負担に。"
              width={375}
              height={93}
            />
          ) : (
            <BannerImg
              src={BannerPCImage}
              alt="「IT導入補助金」のご活用により月額利用料の最大2年分の補助で1/4の実質費用負担に。"
              width={401}
              height={146}
            />
          )}
        </Banner>
        <CloseButton onClick={() => setHasPushedCloseButton(true)}>
          <CloseButtonImg src={ButtonImage} alt="" width={closeButtonSize} height={closeButtonSize} />
        </CloseButton>
      </Contents>
    </Container>
  )
}

const shouldRender = (isScrolled: boolean, hasPushedCloseButton: boolean) => {
  return isScrolled && !hasPushedCloseButton
}
